.popup__container {    
    width: 430px;
    background-color: #fff;
    color: black;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    margin:0 auto;
    position: fixed;
}

@media screen and (max-width:768px) {
    .popup__container {
        width: 282px;
    }
}